define(['jquery'], function ($) {
    return {
        init: function () {
            // TODOJH: Remove
            // alert('anchor2collapsed.js');
            // console.log('Entered anchor2collapsed.js');
            $(function () {
                $(document).on('click', 'a[href^="#"]:not([href="#"]):not([data-toggle="collapse"]):not([aria-expanded])',
                    function (ev) {
                        var targetId = $(ev.target).attr('href'),
                            $target = $(targetId);
                        if ($target.hasClass('collapse')) {
                            $target.addClass('show');
                        }
                        $target.parents('.collapse').addClass('show');
                    });
            });
        }
    };
});
